				<img :src="item.procPic" alt="">
			</view>
			<view class="shop_cont">
				<view class="shop_title">
					{{item.procTitle}}
				</view>
				<view class="shop_fl">
					{{item.typeName}}
				</view>
				<view class="shop_gg">
					{{item.params}}
				</view>
				<view class="shop_time">
					<view class="time">
						<img src="../../static/img/time.png" alt="">

						<view class="time_show">
							{{item.sendTime}}
						</view>
					</view>
					<view class="shop_people">
						<img src="../../static/img/eyes.png" alt="">
