							58694
						</view>
					</view>
				</view>
				<view class="shop_zx">
					<view class="shop_address">
						{{item.companyAddress}}
					</view>
					<view class="shop_change" @click="onlineCard">
						<img src="../../static/img/xx.png" alt="">
