							询单
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			tabCont: {
				type: Array,
				default: []
			},

		},
		data() {
			return {

			}
		},
		methods: {
			typeDetil() {
				this.$emit('click')
			}
		}
	}
</script>

<style>
	.shop_p {
		padding: 27rpx 20rpx 0;
		box-sizing: border-box;
	}

	.shop_type {
		padding: 20rpx 18rpx;
		color: #fff;
		display: flex;
		justify-content: space-between;
		background-color: #fff;
		border-radius: 6px;
	}

	.shop_img {
		width: 220rpx;
		height: 220rpx;
		border-radius: 6px;
	}

	.shop_img img {
		width: 220rpx;
		height: 220rpx;
		background-color: #f00;
	}

	.shop_cont {
		width: 440rpx;
		height: 100%;
	}

	.shop_cont .shop_title {
		font-size: 36rpx;
		color: #333333;
		font-weight: bold;

	}

	.shop_cont .shop_fl {
		font-size: 26rpx;
		color: #666666;
		padding: 10rpx 0;
	}

	.shop_gg {
		font-size: 26rpx;
		color: #999;
		padding: 0rpx 0 10rpx;
	}

	.shop_cont .shop_time {
		display: flex;
	}

	.shop_cont .shop_time .time {
		display: flex;
		justify-content: center;
		margin-right: 90rpx;
	}

