		display: flex;
	}

	.shop_people img {
		width: 32rpx;
		height: 32rpx;
	}

	.eyes {
		margin-left: 12rpx;
		font-size: 26rpx;
		color: #999999;
	}

	.shop_zx {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin-top: 20rpx;
	}

